'use strict';

/**
* Compute the mid-range of a double-precision floating-point strided array.
*
* @module @stdlib/stats-strided-dmidrange
*
* @example
* import Float64Array from '@stdlib/array-float64';
* import dmidrange from '@stdlib/stats-strided-dmidrange';
*
* var x = new Float64Array( [ 1.0, -2.0, 2.0 ] );
*
* var v = dmidrange( x.length, x, 1 );
* // returns 0.0
*
* @example
* import Float64Array from '@stdlib/array-float64';
* import floor from '@stdlib/math-base-special-floor';
* import dmidrange from '@stdlib/stats-strided-dmidrange';
*
* var x = new Float64Array( [ 2.0, 1.0, 2.0, -2.0, -2.0, 2.0, 3.0, 4.0 ] );
*
* var v = dmidrange.ndarray( 4, x, 2, 1 );
* // returns 1.0
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils-try-require';
import isError from '@stdlib/assert-is-error';
import main from './main.js';


// MAIN //

var dmidrange;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	dmidrange = main;
} else {
	dmidrange = tmp;
}


// EXPORTS //

export default dmidrange;

// exports: { "ndarray": "dmidrange.ndarray" }
