'use strict';

/**
* Compute the standard deviation of a single-precision floating-point strided array using a one-pass textbook algorithm.
*
* @module @stdlib/stats-strided-sstdevtk
*
* @example
* import Float32Array from '@stdlib/array-float32';
* import sstdevtk from '@stdlib/stats-strided-sstdevtk';
*
* var x = new Float32Array( [ 1.0, -2.0, 2.0 ] );
*
* var v = sstdevtk( x.length, 1, x, 1 );
* // returns ~2.0817
*
* @example
* import Float32Array from '@stdlib/array-float32';
* import sstdevtk from '@stdlib/stats-strided-sstdevtk';
*
* var x = new Float32Array( [ 2.0, 1.0, 2.0, -2.0, -2.0, 2.0, 3.0, 4.0 ] );
*
* var v = sstdevtk.ndarray( 4, 1, x, 2, 1 );
* // returns 2.5
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils-try-require';
import isError from '@stdlib/assert-is-error';
import main from './main.js';


// MAIN //

var sstdevtk;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	sstdevtk = main;
} else {
	sstdevtk = tmp;
}


// EXPORTS //

export default sstdevtk;

// exports: { "ndarray": "sstdevtk.ndarray" }
