'use strict';

/**
* Compute the maximum absolute value of a double-precision floating-point strided array.
*
* @module @stdlib/stats-strided-dmaxabs
*
* @example
* import Float64Array from '@stdlib/array-float64';
* import dmaxabs from '@stdlib/stats-strided-dmaxabs';
*
* var x = new Float64Array( [ 1.0, -2.0, 2.0 ] );
*
* var v = dmaxabs( x.length, x, 1 );
* // returns 2.0
*
* @example
* import Float64Array from '@stdlib/array-float64';
* import dmaxabs from '@stdlib/stats-strided-dmaxabs';
*
* var x = new Float64Array( [ 2.0, 1.0, 2.0, -2.0, -2.0, 2.0, 3.0, 4.0 ] );
*
* var v = dmaxabs.ndarray( 4, x, 2, 1 );
* // returns 4.0
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils-try-require';
import isError from '@stdlib/assert-is-error';
import main from './main.js';


// MAIN //

var dmaxabs;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	dmaxabs = main;
} else {
	dmaxabs = tmp;
}


// EXPORTS //

export default dmaxabs;

// exports: { "ndarray": "dmaxabs.ndarray" }
