'use strict';

/**
* Compute the variance of a double-precision floating-point strided array using a two-pass algorithm.
*
* @module @stdlib/stats-strided-dvariancepn
*
* @example
* import Float64Array from '@stdlib/array-float64';
* import dvariancepn from '@stdlib/stats-strided-dvariancepn';
*
* var x = new Float64Array( [ 1.0, -2.0, 2.0 ] );
*
* var v = dvariancepn( x.length, 1, x, 1 );
* // returns ~4.3333
*
* @example
* import Float64Array from '@stdlib/array-float64';
* import dvariancepn from '@stdlib/stats-strided-dvariancepn';
*
* var x = new Float64Array( [ 2.0, 1.0, 2.0, -2.0, -2.0, 2.0, 3.0, 4.0 ] );
*
* var v = dvariancepn.ndarray( 4, 1, x, 2, 1 );
* // returns 6.25
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils-try-require';
import isError from '@stdlib/assert-is-error';
import main from './main.js';


// MAIN //

var dvariancepn;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	dvariancepn = main;
} else {
	dvariancepn = tmp;
}


// EXPORTS //

export default dvariancepn;

// exports: { "ndarray": "dvariancepn.ndarray" }
