'use strict';

/**
* Compute the variance of a double-precision floating-point strided array ignoring `NaN` values and using a one-pass textbook algorithm.
*
* @module @stdlib/stats-strided-dnanvariancetk
*
* @example
* import Float64Array from '@stdlib/array-float64';
* import dnanvariancetk from '@stdlib/stats-strided-dnanvariancetk';
*
* var x = new Float64Array( [ 1.0, -2.0, NaN, 2.0 ] );
*
* var v = dnanvariancetk( x.length, 1, x, 1 );
* // returns ~4.3333
*
* @example
* import Float64Array from '@stdlib/array-float64';
* import dnanvariancetk from '@stdlib/stats-strided-dnanvariancetk';
*
* var x = new Float64Array( [ 2.0, 1.0, 2.0, -2.0, -2.0, 2.0, 3.0, 4.0, NaN, NaN ] );
*
* var v = dnanvariancetk.ndarray( 5, 1, x, 2, 1 );
* // returns 6.25
*/

// MODULES //

var join = require( 'path' ).join;
import tryRequire from '@stdlib/utils-try-require';
import isError from '@stdlib/assert-is-error';
import main from './main.js';


// MAIN //

var dnanvariancetk;
var tmp = tryRequire( join( __dirname, './native.js' ) );
if ( isError( tmp ) ) {
	dnanvariancetk = main;
} else {
	dnanvariancetk = tmp;
}


// EXPORTS //

export default dnanvariancetk;

// exports: { "ndarray": "dnanvariancetk.ndarray" }
